import React from "react";
import man1 from "../../static/images/customers/man1.png";
import man2 from "../../static/images/customers/man2.png";
import man3 from "../../static/images/customers/man3.png";
import man4 from "../../static/images/customers/man4.png";
import man5 from "../../static/images/customers/man5.png";
import man6 from "../../static/images/customers/man6.png";
import Layout from "../components/layout";
import { Helmet } from "react-helmet";

const Reference = () => {
  const data = [
    {
      name: "Jana Novotná",
      text: "S Jakubem jsme byli naprosto spokojeni. Nafotil pro nás několik portrétů a výsledek byl úžasný! Dokázal zachytit naše radostné okamžiky s takovou přirozeností. Určitě doporučujeme a těšíme se na další focení! 😊📸",
      imgSrc: man1,
    },
    {
      name: "Petra Šmídová",
      text: "Jakub nám nafotil několik produktových fotek a nemohli jsme být šťastnější s výsledkem! Skvěle zachytil všechny detaily a barvy. Má profesionální a přátelský přístup, rozhodně doporučuji! ❤️📸",
      imgSrc: man2,
    },
    {
      name: "Tomáš Dvořák",
      text: "Naprosto perfektní zkušenost! Jakub nám fotil portréty v parku a byl to skvělý zážitek. Dokonce i náš pes, který obvykle nespolupracuje, se zdál být nadšený z focení. 😊🐾 Profesionální, příjemný a výsledky jsou fantastické!",
      imgSrc: man3,
    },
    {
      name: "Klára Veselá",
      text: "Děkujeme za úžasné fotky přírody! Jakub byl skvělý a vše probíhalo hladce. Jeho pozornost k detailům je ohromující a fotografie jsou plné krásných momentů, které si budeme navždy pamatovat. Můžeme jen a jen doporučit! 📸💕",
      imgSrc: man4,
    },
    {
      name: "Milan Král",
      text: "Jakub je výborný fotograf. Nafotil několik portrétů a zároveň zachytil krásné momenty v přírodě. Skvěle zpracoval všechny detaily, které teď můžeme sdílet s rodinou. Velmi příjemná spolupráce a výsledek je nádherný! 😊📷",
      imgSrc: man5,
    },
    {
      name: "Jan Netík",
      text: "Ještě jednou moc děkujeme za skvělé portrétové focení👍🏼 Vše proběhlo v pohodové atmosféře, a výsledek byl parádní! Jsme moc spokojeni, a mohu jen doporučit!😊",
      imgSrc: man6,
    },
  ];

  return (
    <Layout>
      <Helmet>
        <title>Reference | Jakub Pšenička | Fotograf</title>
        <meta
          name="description"
          content="Zabívám se tvorbou videii a focením všech možných událostí. Na mých stránkách si můžete prohlednou ukázku mé tvorby, přečíst reference či mě rovnou kontaktovat."
        ></meta>
      </Helmet>
      <div>
        <div className="container my-5">
          <div className="text-center h1 mb-5">Reference</div>
          <hr />
          {data.map((item, index) => {
            return (
              <div key={index}>
                <div className="row">
                  <div className="col-12 col-lg-2 text-center">
                    <img
                      src={item.imgSrc}
                      className="rounded-circle"
                      alt={"recenze od " + item.name}
                    />
                  </div>
                  <div className="col-12 col-lg-10">
                    <p className="mt-3">{item.text}</p>
                    <p className="text-end mr-2 mt-4">- {item.name}</p>
                  </div>
                </div>
                <hr />
              </div>
            );
          })}
        </div>
      </div>
    </Layout>
  );
};

export default Reference;
